import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import CheckoutSteps from '../components/CheckoutSteps'
import {Link} from 'react-router-dom'
import { createOrder } from '../actions/orderActions'
import { ORDER_CREATE_RESET } from '../constants/orderConstants'


export default function PlaceOrderScreen(props) {
    const cart=useSelector((state)=>state.cart)
    if(!cart.paymentMethod)
    {
        props.history.push('/payment')
    }
   const dispatch =useDispatch();
     const toPrice=(num)=>Number(num.toFixed(2)) 
     
    //  5.125='5.12'=Number()=5.12
    cart.itemsPrice=toPrice(cart.cartItems.reduce((a,c)=>a+c.qty*c.price,0))
    cart.shippingPrice=cart.itemsPrice>100?toPrice(0):toPrice(10)
    cart.taxPrice=toPrice(0.15*cart.itemsPrice);
    cart.totalPrice=cart.itemsPrice+cart.shippingPrice+cart.taxPrice
    const placeOrderHandler=()=>{
        //todo
        dispatch(createOrder({...cart,orderItems:cart.cartItems}))
    }
    const orderCreate=useSelector(state=>state.orderCreate)
    const {loading,success,error,order}=orderCreate;
    useEffect(() => {
        if(success)
        {
            
            props.history.push(`/order/${order._id}`)
            dispatch({type:ORDER_CREATE_RESET})
            
        }
       
    }, [dispatch,order,props.history,success])
    return (
        <div>
            <CheckoutSteps step1 step2 step3 step4></CheckoutSteps>
            <div className="row top">
                <div className="col2">
                    <ul>
                        <li>
                            <div className="card card-body">
                                <h1>Shipping</h1>
                                <p>
                                    <strong>Name:</strong>{cart.shippingAddress.fullName} <br></br>
                                    <strong>Address:</strong>{cart.shippingAddress.address},
                                    {cart.shippingAddress.city},{cart.shippingAddress.postalCode},{cart.shippingAddress.country}

                                </p>
                            </div>
                        </li>
                        <li>
                            <div className="card card-body">
                                <h1>Payment</h1>
                                <p>
                                    <strong>Method:</strong>{cart.paymentMethod} 
                                </p>
                            </div>
                        </li>
                        <li>
                            <div className="card card-body">
                                <h1>Order Items</h1>
                                <ul>
                        
                        {cart.cartItems.map((item)=>(
                            
                            <li key={item.product}>
                                <div className="row colour">
                                    <div>
                                        <img src={item.image} className='small'></img>
                                    </div>
                                    <div className="min-39">
                                        <Link to={`/products/${item.product}`}>{item.name}</Link>
                                    </div>
                                   
                                    <div><b>{item.qty} x ${item.price}=${item.qty*item.price}</b></div>
                                   
                                </div>
                            </li>
                            
                        ))}
                    </ul>
                               </div>
                        </li>
                    </ul>

                </div>
              

            
                <div className="col1">
                    <div className="card card-body">
                        <ul>
                            <li>
                                <h2>Order Summary</h2>
                            </li>
                            <li>
                                <div className="row">
                                    <div>Items</div>
                                    <div>${cart.itemsPrice.toFixed(2)}</div>
                                </div>
                            </li>
                            <li>
                                <div className="row">
                                    <div>Shipping</div>
                                    <div>${cart.shippingPrice.toFixed(2)}</div>
                                </div>
                            </li>
                            <li>
                                <div className="row">
                                    <div>Tax</div>
                                    <div>${cart.taxPrice.toFixed(2)}</div>
                                </div>
                            </li>
                            <li>
                                <div className="row">
                                    <div><strong>Order Total</strong></div>
                                    <div><strong>${cart.totalPrice.toFixed(2)}</strong></div>
                                </div>
                            </li>
                           
                            <li>
                                
                            <button type="button"  className="primary block" onClick={placeOrderHandler}>PlacrOrder</button>
                                
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}
